@import url(https://fonts.googleapis.com/css?family=Karla);
.App {
  width: 100%;
  height: 100%;
  display: flex; }

#root {
  width: 100%;
  height: 100%; }

html {
  width: 100%;
  height: 100%;
  font-family: 'Karla', sans-serif;
  font-size: 2.3vh;
  line-height: 3.8vh;
  scroll-behavior: smooth; }

body {
  width: calc(250vw - 30px);
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #181818; }
  body::-webkit-scrollbar {
    width: 0;
    height: 0; }

.bold {
  font-weight: bold; }

.italic {
  font-style: italic; }

.device-wrapper {
  height: 100%;
  font-size: 2.3vh;
  line-height: 3.8vh; }

.mobile {
  width: 100vw; }
  .mobile.device-wrapper {
    height: 100%;
    font-size: 18px;
    line-height: 30px; }
  .mobile .App {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column; }
  .mobile body {
    width: 100%;
    height: auto; }
    .mobile body::-webkit-scrollbar {
      width: auto;
      height: auto; }

.Navigation {
  position: fixed;
  display: flex;
  height: 5.3vh;
  width: 100vw;
  align-items: center;
  justify-content: space-between;
  background-color: #FFFFFF;
  padding: 0 4vw;
  box-sizing: border-box; }
  .Navigation .logo {
    font-weight: bold; }
    .Navigation .logo::-moz-selection {
      background: rgba(24, 24, 24, 0.99);
      color: #FFFFFF; }
    .Navigation .logo::selection {
      background: rgba(24, 24, 24, 0.99);
      color: #FFFFFF; }
    .Navigation .logo::-moz-selection {
      background: rgba(24, 24, 24, 0.99);
      color: #FFFFFF; }
  .Navigation ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 100%;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
    .Navigation ul li {
      display: inline-flex;
      height: 100%;
      padding: 0 20px;
      align-items: center;
      -webkit-transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
      transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1); }
      .Navigation ul li a {
        text-decoration: none;
        color: #181818; }
        .Navigation ul li a::-moz-selection {
          background: rgba(24, 24, 24, 0.99);
          color: #FFFFFF; }
        .Navigation ul li a::selection {
          background: rgba(24, 24, 24, 0.99);
          color: #FFFFFF; }
        .Navigation ul li a::-moz-selection {
          background: rgba(24, 24, 24, 0.99);
          color: #FFFFFF; }
      .Navigation ul li:hover, .Navigation ul li:hover + li, .Navigation ul li.hover {
        box-shadow: inset 0px -0.26vh 0px 0px #181818;
        -webkit-transition: box-shadow 200ms cubic-bezier(0.4, 0, 0.2, 1);
        transition: box-shadow 200ms cubic-bezier(0.4, 0, 0.2, 1); }
  .Navigation .scrollbar {
    position: absolute;
    top: 0;
    width: 25vw;
    height: .26vh;
    background-color: #181818; }

.mobile .Navigation {
  display: none; }

.Block {
  display: flex;
  justify-content: center;
  width: 49.3vw;
  height: calc(100% - 5.3vh);
  box-sizing: border-box;
  margin-top: 5.3vh;
  padding-top: 10vw; }

.text-section {
  width: 80%; }
  .text-section .text-section-title {
    font-size: 2em;
    font-weight: bold; }
  .text-section p {
    margin: 30px 0; }

